<template>
  <div class="Pagination" v-if="pagesTotal > 1">
    <ul class="Pagination__list">
      <li class="Pagination__item" :class="prevDisabledClass" @click="goTo(currentPage - 1)">Prev</li>
      <li class="Pagination__item" :class="setActiveClass(1)" @click="goTo(1)">1</li>
      <li v-if="hasFirstDots" class="Pagination__item">...</li>
      <li class="Pagination__item"
          v-for="page in inRange"
          :class="setActiveClass(page)"
          :key="page" @click="goTo(page)">
        {{ page }}
      </li>
      <li v-if="hasLastDots" class="Pagination__item">...</li>
      <li class="Pagination__item" :class="setActiveClass(pagesTotal)" @click="goTo(pagesTotal)">{{ pagesTotal }}</li>
      <li class="Pagination__item" :class="nextDisabledClass" @click="goTo(currentPage + 1)">Next</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Pagination',
  props: {
    pagesTotal: {
      type: Number,
      required: true
    },
    currentPage: {
      type: Number,
      required: false,
      default: 1
    },
    delta: {
      type: Number,
      required: false,
      default: 2
    }
  },
  computed: {
    prevDisabledClass () {
      return {
        'disabled': this.currentPage === 1
      }
    },
    nextDisabledClass () {
      return {
        'disabled': this.currentPage === this.pagesTotal
      }
    },
    hasFirstDots () {
      return this.currentPage - this.delta > 2
    },
    hasLastDots () {
      return this.currentPage + this.delta < this.pagesTotal - 1
    },
    inRange () {
      const left = this.currentPage - this.delta
      const right = this.currentPage + this.delta
      const range = []

      for (let i = 1; i <= this.pagesTotal; i++) {
        if (i >= left && i <= right && i !== 1 && i !== this.pagesTotal) {
          range.push(i)
        }
      }

      return range
    }
  },
  methods: {
    goTo (page) {
      if (page === this.currentPage) return
      if (page >= 1 && page <= this.pagesTotal) {
        this.$emit('updatePage', page)
      }
    },
    setActiveClass (page) {
      if (page === this.currentPage) return 'active'
    }
  }
}
</script>

<style lang="scss">
.Pagination__list {
  margin: 20px 0;
  padding: 0;
  list-style: none;
  text-align: center;
}

.Pagination__item {
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  margin: 0 2px;
}

.Pagination .active {
  background-color: red;
}

.Pagination .disabled {
  color: #ccc;
  cursor: default;
}
</style>
