<template>
  <div>
    <div v-if="articles.length" class="article-list">
      <div class="Article" v-for="(post, index) in articles" :key="index">
        <img :src="post.author.image">
        <router-link :to="{name: 'ProfileAuthor', params: {name: post.author.username}}">{{ post.author.username }}</router-link>
        <div>{{ post.createdAt || moment("dddd, MMMM Do YYYY") }}</div>
        <h3>{{ post.title }}</h3>
        <p>{{ post.body }}</p>
      </div>
      <pagination :pagesTotal="pagesTotal" :currentPage="currentPage" @updatePage="changePage" />
    </div>
    <div v-if="message">{{ message }}</div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Pagination from '@/components/Pagination'

export default {
  name: 'Articles',
  components: {
    Pagination
  },
  props: {
    type: {
      type: String,
      default: 'global' // my
    },
    author: {
      type: String,
      required: false
    },
    tag: {
      type: String,
      required: false
    },
    favorited: {
      type: String,
      required: false
    },
    perPage: {
      type: Number,
      required: false,
      default: 10
    }
  },
  data () {
    return {
      currentPage: 1
    }
  },
  computed: {
    config () {
      const filters = {
        offset: (this.currentPage - 1) * this.perPage,
        limit: this.perPage
      }
      const type = this.type
      if (this.author) {
        filters.author = this.author
      }
      if (this.tag) {
        filters.tag = this.tag
      }
      if (this.favorited) {
        filters.favorited = this.favorited
      }
      return {
        type,
        filters
      }
    },
    pagesTotal () {
      return Math.ceil(this.total / this.perPage)
    },
    ...mapGetters({
      articles: 'articles/getList',
      total: 'articles/getTotal',
      message: 'articles/getMessage'
    })
  },
  created () {
    this.fetchArticles()
  },
  watch: {
    author () {
      this.resetPagination()
      this.fetchArticles()
    },
    tag () {
      this.resetPagination()
      this.fetchArticles()
    },
    favorited () {
      this.resetPagination()
      this.fetchArticles()
    },
    type () {
      this.resetPagination()
      this.fetchArticles()
    }
  },
  methods: {
    fetchArticles () {
      this.$store.dispatch('articles/fetchArticles', this.config)
    },
    changePage (page) {
      this.currentPage = page
      this.fetchArticles()
    },
    resetPagination () {
      this.currentPage = 1
    }
  }
}
</script>

<style lang="scss">
.Article {
  border: solid #000;
  border-width: 2px 0;
  padding: 10px 0;
}
</style>
